"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { User, GraduationCap, Briefcase, FileText, FolderGit2, LogOut, Calendar, Bell, Award } from "lucide-react"
import { BasicInfoTab } from "@/components/student/basic-info-tab"
import { AcademicsTab } from "@/components/student/academics-tab"
import { InternshipsTab } from "@/components/student/internships-tab"
import { ResumesTab } from "@/components/student/resumes-tab"
import { ProjectsTab } from "@/components/student/projects-tab"
import { CertificatesTab } from "@/components/student/certificates-tab"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"

import { useSession } from "next-auth/react"

export default function StudentDashboard() {
  const router = useRouter()
  const { data: session, status } = useSession()
  const [user, setUser] = useState<any>(null)
  const [studentData, setStudentData] = useState<any>(null)

  useEffect(() => {
    if (status === "loading") return
    if (status === "unauthenticated") {
      router.push("/")
      return
    }

    const userData = session?.user
    if (!userData || userData.role !== "student") {
      if (userData?.role) {
        router.push(`/dashboard/${userData.role}`)
      } else {
        router.push("/")
      }
      return
    }

    setUser(userData)

    // Load student data from localStorage based on session ID/Name mapping or fallback
    // Since mock auth just gives us an email/id, we try to match it with localStorage data
    try {
      const students = JSON.parse(localStorage.getItem("students") || "[]")
      // If userData has a specific studentId (passed from auth token ideally), use it.
      // For now, we might need to rely on matching name or email if id isn't synced perfectly in mock.
      // In the login form we used to map IDs.

      // Fallback: finding first student or matching by email if possible
      // IMP: Using fuzzy match or strict roll number match from session
      let student = null;

      // 1. Try matching by Roll Number (stored in session.user.id for Roll No logins)
      // The session ID might be the roll number "231cg004"
      if (userData.id && userData.id !== "2") {
        student = students.find((s: any) => s.rollNumber?.toLowerCase() === userData.id?.toLowerCase())
      }

      // 2. If not found, try matching by Email
      if (!student) {
        student = students.find((s: any) => s.email?.toLowerCase() === userData.email?.toLowerCase())
      }

      // 3. If still not found, and it IS the demo user (id="2"), use the first one or a mock one
      if (!student && userData.id === "2") {
        student = students[0];
      }

      if (student) {
        setStudentData(student)
      } else {
        // If we logged in but can't find data (e.g. valid credentials but no profile in localStorage yet)
        // Show a placeholder or the session info
        setStudentData({
          id: userData.id || "unknown",
          rollNumber: userData.name?.replace("Student ", "") || "Unknown", // extraction
          year: "I", // Default
          section: "A",
          department: "CSE",
          name: userData.name || "Student"
        })
      }
    } catch (e) {
      console.error("Error loading student data", e)
    }
  }, [session, status, router])

  const handleLogout = async () => {
    const { signOut } = await import("next-auth/react")
    await signOut({ redirect: true, callbackUrl: "/" })
  }

  if (!user || !studentData) {
    return null
  }

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-semibold">Student Portal</h1>
              <p className="text-sm text-muted-foreground">
                Welcome, {user.name} ({studentData.rollNumber})
              </p>
            </div>
            <Button variant="outline" onClick={handleLogout}>
              <LogOut className="h-4 w-4 mr-2" />
              Logout
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="grid gap-4 md:grid-cols-2 mb-8">
          <Card
            className="cursor-pointer hover:shadow-md transition-shadow"
            onClick={() => router.push("/dashboard/student/timetable")}
          >
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Calendar className="h-5 w-5" />
                View Timetable
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">Check your class schedule and timings</p>
            </CardContent>
          </Card>

          <Card
            className="cursor-pointer hover:shadow-md transition-shadow"
            onClick={() => router.push("/dashboard/student/notifications")}
          >
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Bell className="h-5 w-5" />
                Notifications
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">View announcements and updates</p>
            </CardContent>
          </Card>
        </div>

        <Tabs defaultValue="basic" className="space-y-6">
          <TabsList className="grid w-full grid-cols-6">
            <TabsTrigger value="basic">
              <User className="h-4 w-4 mr-2" />
              Basic Info
            </TabsTrigger>
            <TabsTrigger value="academics">
              <GraduationCap className="h-4 w-4 mr-2" />
              Academics
            </TabsTrigger>
            <TabsTrigger value="internships">
              <Briefcase className="h-4 w-4 mr-2" />
              Internships
            </TabsTrigger>
            <TabsTrigger value="resumes">
              <FileText className="h-4 w-4 mr-2" />
              Resumes
            </TabsTrigger>
            <TabsTrigger value="certificates">
              <Award className="h-4 w-4 mr-2" />
              Certificates
            </TabsTrigger>
            <TabsTrigger value="projects">
              <FolderGit2 className="h-4 w-4 mr-2" />
              Projects
            </TabsTrigger>
          </TabsList>

          <TabsContent value="basic">
            <BasicInfoTab studentData={studentData} studentId={studentData.id} />
          </TabsContent>

          <TabsContent value="academics">
            <AcademicsTab studentId={studentData.id} />
          </TabsContent>

          <TabsContent value="internships">
            <InternshipsTab studentId={studentData.id} />
          </TabsContent>

          <TabsContent value="resumes">
            <ResumesTab studentId={studentData.id} />
          </TabsContent>

          <TabsContent value="certificates">
            <CertificatesTab studentId={studentData.id} />
          </TabsContent>

          <TabsContent value="projects">
            <ProjectsTab studentId={studentData.id} studentYear={studentData.year} />
          </TabsContent>
        </Tabs>
      </main>
    </div>
  )
}
