"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Plus, Trash2, Upload, Check } from "lucide-react"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"

type Resume = {
  id: string
  name: string
  fileUrl: string
  fileName?: string // Added
  uploadedAt: string
  isActive: boolean
}

type ResumesProps = {
  studentId: string
}

export function ResumesTab({ studentId }: ResumesProps) {
  const [resumes, setResumes] = useState<Resume[]>([])
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [formData, setFormData] = useState({
    name: "",
    fileUrl: "",
    fileName: "",
  })

  const fetchResumes = async () => {
    try {
      const token = localStorage.getItem("token");
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/students/${studentId}`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      if (res.ok) {
        const data = await res.json();
        // Backend returns student with resumes array. 
        // We map it to our frontend type. 
        // Note: Backend might not return isActive. We'll simulate it by picking the first one or just ignoring it for persistence.
        if (data.resumes) {
          const mapped = data.resumes.map((r: any, index: number) => ({
            id: r.id,
            name: r.title,
            fileUrl: r.fileUrl,
            uploadedAt: r.uploadedAt,
            isActive: index === 0 // Default first to active
          }));
          setResumes(mapped);
        }
      }
    } catch (error) {
      console.error("Failed to fetch resumes", error);
    }
  }

  useEffect(() => {
    fetchResumes();
  }, [studentId])

  const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      // Upload to endpoint
      const uploadData = new FormData();
      uploadData.append('file', file);

      try {
        const token = localStorage.getItem("token");
        // Use specific resume upload endpoint if distinct folder needed, or generic. 
        // usage of /upload/resume puts it in 'resumes' folder.
        const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/upload/resume`, {
          method: 'POST',
          headers: { 'Authorization': `Bearer ${token}` },
          body: uploadData
        });

        if (res.ok) {
          const data = await res.json();
          setFormData({
            ...formData,
            fileUrl: data.url,
            fileName: file.name
          });
        } else {
          alert("File upload failed");
        }
      } catch (error) {
        console.error("Upload error", error);
        alert("File upload error");
      }
    }
  }

  const handleAdd = async () => {
    try {
      const token = localStorage.getItem("token");
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/students/${studentId}/resumes`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          name: formData.name,
          fileUrl: formData.fileUrl
        })
      });

      if (res.ok) {
        setFormData({ name: "", fileUrl: "", fileName: "" });
        setIsDialogOpen(false);
        fetchResumes(); // Refresh list
      } else {
        alert("Failed to save resume");
      }
    } catch (error) {
      console.error("Save error", error);
    }
  }

  const handleDelete = async (id: string) => {
    try {
      const token = localStorage.getItem("token");
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/students/${studentId}/resumes/${id}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });

      if (res.ok) {
        fetchResumes();
      } else {
        alert("Failed to delete resume");
      }
    } catch (error) {
      console.error("Delete error", error);
    }
  }

  const handleSetActive = (id: string) => {
    // Only local toggle for now as backend doesn't support isActive
    const updated = resumes.map((r) => ({ ...r, isActive: r.id === id }))
    setResumes(updated)
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-semibold">Resumes</h2>
          <p className="text-sm text-muted-foreground">Upload multiple resume versions and set one as active</p>
        </div>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="h-4 w-4 mr-2" />
              Upload Resume
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Upload New Resume</DialogTitle>
            </DialogHeader>
            <div className="space-y-4 mt-4">
              <div className="space-y-2">
                <Label htmlFor="resumeName">Resume Name *</Label>
                <Input
                  id="resumeName"
                  placeholder="e.g., Software Developer Resume, Data Science Resume"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="resumeFile">Upload File *</Label>
                <Label htmlFor="resumeFile" className="cursor-pointer">
                  <div className="flex items-center gap-2 px-4 py-2 border rounded-md hover:bg-accent">
                    <Upload className="h-4 w-4" />
                    {formData.fileUrl ? "Resume Uploaded" : "Choose PDF File"}
                  </div>
                </Label>
                <Input
                  id="resumeFile"
                  type="file"
                  accept="application/pdf"
                  className="hidden"
                  onChange={handleFileUpload}
                />
              </div>

              <div className="flex justify-end gap-2 pt-4">
                <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
                  Cancel
                </Button>
                <Button onClick={handleAdd} disabled={!formData.name || !formData.fileUrl}>
                  Upload Resume
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {resumes.length === 0 ? (
        <Card>
          <CardContent className="text-center py-12">
            <p className="text-muted-foreground">No resumes uploaded yet</p>
            <p className="text-sm text-muted-foreground mt-2">Upload your resume to apply for placements</p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4">
          {resumes.map((resume) => (
            <Card key={resume.id} className={resume.isActive ? "border-primary" : ""}>
              <CardHeader>
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center gap-2">
                      <CardTitle className="text-lg">{resume.name}</CardTitle>
                      {resume.isActive && (
                        <Badge variant="default" className="bg-green-500 hover:bg-green-600">
                          Active
                        </Badge>
                      )}
                    </div>
                    <p className="text-sm text-muted-foreground mt-1">
                      Uploaded on {new Date(resume.uploadedAt).toLocaleDateString()}
                    </p>
                  </div>
                  <div className="flex gap-2">
                    {!resume.isActive && (
                      <Button variant="outline" size="sm" onClick={() => handleSetActive(resume.id)}>
                        <Check className="h-4 w-4 mr-2" />
                        Set Active
                      </Button>
                    )}
                    <Button variant="ghost" size="sm" onClick={() => handleDelete(resume.id)}>
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              </CardHeader>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
